<script>
import {mapState,mapMutations, mapActions} from 'vuex';
export default {
        data(){
        return{
           productos:[],
           prodAdd:{},
           mensaje: {color: '', texto: ''},
           dismissSecs: 2,
           dismissCountDown: 0
                
        }
    },
    computed:{
      ...mapState(['estaCompra','total','categoria'])
    },
    created(){
        this.ObtenerProductos();
    },
    methods:{
      ...mapMutations(['AgregarAlCarrito','AgregarOrden']),
        ObtenerProductos(){
           this.axios.get('/productos') 
             .then(res=>{
 
                this.productos=res.data;
              })
             .catch(e=>{
               
           })
        },
        compra(id){
          this.axios.get(`/producto/${id}`)
            .then(res=>{
              this.prodAdd=res.data;
              
              this.$store.commit('AgregarAlCarrito',this.prodAdd);
              this.$store.commit('AgregarOrden',this.prodAdd);
              this.mensaje.color='success';
              this.mensaje.texto=this.prodAdd.name + ' Agregado Al Carrito';
              this.showAlert();
            })
            .catch(e=>{
              console.log(e.response,"error")
            })
        },
        countDownChanged(dismissCountDown) {
          this.dismissCountDown = dismissCountDown
        },
        showAlert() {
          this.dismissCountDown = this.dismissSecs
        }
    }

}
</script>

